import { makeStyles, Button  } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import {v4 as uuid} from 'uuid';

const useStyles = makeStyles({
    otsikko : {
        margin : "10px",
        fontSize : "20px",
        fontWeight : "bold",
        fontFamily : "verdana",
    },
    teksti : {
        margin : "10px",
        fontSize : "14px",
        fontFamily : "verdana",
    },
    tekstikentta : {
        margin : "0px",
        fontSize : "14px",
        fontFamily : "verdana",
        width : "100%",
        backgroundColor : "white",
        marginTop : "5px",
        marginBottom : "10px",
    },
    tekstikenttaUrl : {
        margin : "0px",
        fontSize : "14px",
        fontFamily : "verdana",
        width : "100%",
        backgroundColor : "white",
    },
    kuvaDiv : {
        margin : "auto",
        width : "100%",
    },
    kuva : {
        margin : "auto",
        maxWidth : "99%",
        maxHeight : "99%",
        minWidth : "99%",
        border : "solid black 2px",
    },
    containerMain : {
        maxWidth : "30%",
        minWidth : "150px",
        border : "solid black 1px",
        borderRadius : "25px",
        backgroundColor : "white",
    },
    containerNapit : {
        margin : "auto",
        maxWidth : "100%",
        minWidth : "50px",
        width : "auto",
    },
    nappi : {
        width : "100%",
        margin : "0",
        marginBottom : "5px",
    },
});


function Uusi(props){



    const tyylit = useStyles();
    const history = useHistory();
    const [linkki, setLinkki] = useState();
    const [title, setTitle] = useState();
    const [des, setDes] = useState();
    const [tila, setTila] = useState("");
    const [virhe, setVirhe] = useState();


    const linkinkasittelija = () => {

        let kuva;

        if(linkki){
            kuva = <img src={linkki} alt="Nothing" className={tyylit.kuva}></img>
        } else {
            kuva = <img src="https://complianz.io/wp-content/uploads/2019/03/placeholder-300x202.jpg" alt="Nothing" className={tyylit.kuva}></img>
        }

        return(
            kuva
        )
    }


    const submitkasittelija = (e) => {
        e.preventDefault();

        if(linkki){
            let uusiKuvaApu = {
                url : linkki,
                title : title,
                des : des,
                id : uuid(),
             }
             
             if(!props.kuvat[0].url){
                props.setKuvat([uusiKuvaApu]);
             } else {
                props.setKuvat([...props.kuvat, uusiKuvaApu]);
             }

             setVirhe();
             history.push("/");
        } else {
            setVirhe("*required");
        }
    }
    
    

    return(
        <Container className={tyylit.containerMain}>
            <form onSubmit={submitkasittelija}>
                <br/>
                <div className={tyylit.kuvaDiv}>
                    {linkinkasittelija()}
                </div>
                <br/>
                <TextField
                    id="url-textfield"
                    value={tila}
                    variant="outlined"
                    label="Image URL"
                    error={Boolean(virhe)} 
                    helperText={virhe}
                    className={tyylit.tekstikenttaUrl}
                    onChange={(e) => {
                        setLinkki(e.target.value);
                        setTila(e.target.value);
                    }}
                />
                <br/><br/>
                <div className={tyylit.containerNapit}>
                    <Button variant="contained" color="default" className={tyylit.nappi} onClick={(e) => {
                        setLinkki(null);
                        setTila("");
                    }}>
                        Clear
                    </Button>
                </div>
                <br/><br/>
                <div>
                    <TextField
                        id="title-textfield"
                        variant="outlined"
                        label="Title"
                        className={tyylit.tekstikentta}
                        onChange={(e) => {
                            setTitle(e.target.value);
                        }}
                    />
                </div>
                <div>
                    <TextField
                        id="des-textfield"
                        variant="outlined"
                        label="Description"
                        className={tyylit.tekstikentta}
                        onChange={(e) => {
                            setDes(e.target.value);
                        }}
                    />
                </div>
                <div className={tyylit.containerNapit}>
                    <Button type="submit" variant="contained" color="primary" className={tyylit.nappi}>
                        Submit
                    </Button>
                    <Button variant="contained" color="secondary" className={tyylit.nappi} component={Link} to="/">
                        Cancel
                    </Button>
                </div>
                <br/>
            </form>
        </Container>
    );
}

export default Uusi