import { Grid, makeStyles, Paper } from '@material-ui/core';


const useStyles = makeStyles(() => ({
    root : {
      flexGrow : 1,
    },
    solu : {
      height : "auto",
      width : "auto",
      padding : "5px",
    },
    grid : {
        maxWidth : "400px",
      },
    control : {
      padding : 2,
    },
    des : {
        color : "black",
        margin : "10px",
    },
    title : {
        color : "black",
        margin : "10px",
        textAlign:"center",
    },
  }));


function Kuvalista(props){

    const tyylit = useStyles();

    return(
        <>
            <br/><br/><br/>
            <Grid container className={tyylit.root} >
                <Grid item xs={12}>
                    <Grid container justify="center" spacing={2}>
                    {props.kuvat.map((value, idx) => (
                        <Grid key={idx} item className={tyylit.grid}>
                            <Paper className={tyylit.solu}>
                                <img src={props.kuvat[idx].url} alt="Kuva" width="100%"/>
                                <h3 className={tyylit.title}>{props.kuvat[idx].title}</h3>
                                <p className={tyylit.des} >{props.kuvat[idx].des}</p>
                            </Paper>
                        </Grid>
                    ))}
                    </Grid>
                </Grid>
            </Grid>
        </>
    )
}

export default Kuvalista