import './App.css';
import { useEffect, useState } from 'react';
import {BrowserRouter as Router, Link, Route} from 'react-router-dom';
import Uusi from './components/Uusi';
import Kuvalista from './components/Kuvalista';
import { Button, Container, makeStyles, Typography } from '@material-ui/core';
import {v4 as uuid} from 'uuid';


const useStyles = makeStyles(() => ({
  nappi : {
    width : "33%",
    height : "50px",
    margin : "auto",
    marginLeft : "33%",
    marginTop : "50px",
    fontWeight : "bold",
    fontSize : "18px",
    backgroundColor : "white",
  },

}));


function App() {

  // Muuttujat
  const tyylit = useStyles();
  const [kuvat, setKuvat] = useState([
    {
      url : "https://images-na.ssl-images-amazon.com/images/I/81hrrq3fCwL.png",
      title : "Mad Obama?",
      des : "Funny picture combining obama and mad magazine character.",
      id : uuid(),
    },
    {
      url : "https://i.pinimg.com/originals/75/9b/b4/759bb4497d0b13c3f9d86d9b42ba82e8.jpg",
      title : "wifi",
      des : "wifi logo forming on her forehead.",
      id : uuid(),
    },
    {
      url : "https://filmdaily.co/wp-content/uploads/2020/09/DirtyFunnyMemes-lede72-1300x1115.jpg",
      title : "sleezy face McSleezy",
      des : "",
      id : uuid(),
    },
    {
      url : "https://www.rd.com/wp-content/uploads/2018/12/50-Funny-Animal-Pictures-That-You-Need-In-Your-Life-25.jpg",
      title : "Rise apes",
      des : "'Rise my fellow apes!'",
      id : uuid(),
    },
    {
      url : "https://images.pexels.com/photos/1183434/pexels-photo-1183434.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
      title : "lolcat",
      des : "Funny cat",
      id : uuid(),
    },
    {
      url : "https://resize.indiatvnews.com/en/resize/newbucket/715_-/2017/09/download-1504769921.jpg",
      title : "Goat?",
      des : "Hey baby Kiss the goat Whoa Kiss, kiss, kiss the go-goat You've been playin' around with magic that is black But all the powerful magical mysteries never give a single thing back You've been daddied, by all the dudes that were not dad And all of those dads never gave you the things That you should have had",
      id : uuid(),
    },
  ]);

  // Funktiot
  useEffect(() => {
    avaaKuvalista();
  }, [])

  useEffect(() => {
    tallennaKuvalista();
  }, [kuvat])


  const tallennaKuvalista = () => {
    localStorage.setItem("kuvat", JSON.stringify(kuvat));
  }

  const avaaKuvalista = () => {
    if(localStorage.getItem("kuvat")){
      setKuvat(JSON.parse(localStorage.getItem("kuvat")));
    } else {
      setKuvat(kuvat);
    }
  }
  
  const checkKuvalista = () => {
    if(kuvat[0].url){
      return(
        <Kuvalista kuvat={kuvat} setKuvat={setKuvat}/>
      )
    } else {
      return (
        <Typography>Lisää kuvia!</Typography>
      )
    }
  }
  

  // Paluuarvo
  return (
    <>
    <Router>
      <Container>
            <Typography variant="h1" align="center">
                Piclink
            </Typography>
            <div>
              <Button className={tyylit.nappi} variant="contained" component={Link} to="/Uusi">
                  Uusi kuva
              </Button>
            </div>
          </Container>
      <div>    
        <Route path="/" exact>
          {checkKuvalista()}
        </Route>
        <Route path="/Uusi">
          <Uusi kuvat={kuvat} setKuvat={setKuvat} tallennaKuvalista={tallennaKuvalista}/>
        </Route>
      </div>
    </Router>
    </>
  );
}

export default App;
